import { useState } from 'react'

import { v4 as uuidv4 } from 'uuid'
import Paper from '@mui/material/Paper'
import Box from '@mui/material/Box'

import Panels from './components/Panels'
import Map from './components/Map'
import AutoComplete from './components/AutoComplete'
import './App.css'

function App() {
  const [mapsApiLoaded, setMapsApiLoaded] = useState(false)
  const [mapsApi, setMapsApi] = useState(null)
  const [mapInstance, setMapInstance] = useState(null)
  const [panels, setPanels] = useState([])
  const [editingPanel, setEditingPanel] = useState()

  const handleApiLoaded = ({ map, maps }) => {
    setMapsApi(maps)
    setMapInstance(map)
    setMapsApiLoaded(true)
  }

  const handleOnClick = ({ lat, lng }) => {
    if (editingPanel) {
      const updatedPanels = panels.map(panel => {
        if (panel.id !== editingPanel) {
          return panel
        } else {
          return {
            ...panel,
            coordinates: [...panel.coordinates, { lat, lng }],
          };
        }
      });
      setPanels(updatedPanels)
    }
  }

  const handlePlaceChange = (place) => {
    if (!place.geometry) return;
    if (place.geometry.viewport) {
      mapInstance.fitBounds(place.geometry.viewport)
    } else {
      mapInstance.setCenter(place.geometry.location)
      mapInstance.setZoom(17)
    }
  }

  function handlePolygonComplete(panelId, polygon) {
    console.log({ panelId, polygon })
    setEditingPanel(null)
    polygon.setDrawingMode(null)
    polygon.setOptions({drawingControlOptions: {drawingModes: [] }})
  }

  const handleCreatePanel = () => {
    const newPanelId = uuidv4()
    const polygonColour = `#${Math.floor(Math.random()*16777215).toString(16)}`
    const polygon = new mapsApi.drawing.DrawingManager({
      drawingMode: mapsApi.drawing.OverlayType.POLYGON,
      drawingControl: false,
      drawingControlOptions: {
        position: mapsApi.ControlPosition.TOP_CENTER,
        drawingModes: ['polygon']
      },
      polygonOptions: {
        strokeColor: polygonColour,
        strokeOpacity: 0.8,
        strokeWeight: 2,
        fillColor: polygonColour,
        fillOpacity: 0.6,
        editable: false,
      }
    })
    polygon.setMap(mapInstance)
    mapsApi.event.addListener(polygon, 'polygoncomplete', () => handlePolygonComplete(newPanelId, polygon))   
    setPanels([...panels, { id: newPanelId, isEditing: true, polygon, coordinates: [] }])
    console.log({ newPanelId })
    setEditingPanel(newPanelId)
  }

  return (
    <div className="App">
      <div style={{ height: '100vh', width: '100%', position: 'relative' }}>
        <Map handleApiLoaded={handleApiLoaded} handleOnClick={handleOnClick} />
        {
          mapsApiLoaded && (
            <Paper elevation={4} sx={{ position: 'absolute', top: '20px', left: '20px', width: '50%', p: 2 }}>
              <Box>
                <AutoComplete
                  maps={mapsApi}
                  handlePlaceChange={handlePlaceChange}
                />
              </Box>
              <Panels panels={panels} isEditingPanel={!!editingPanel} handleCreatePanel={handleCreatePanel} />
            </Paper>
          )
        }
      </div>
    </div>
  );
}

export default App;
