import Box from '@mui/material/Box'
import Button from '@mui/material/Button'
import AddIcon from '@mui/icons-material/Add'

import Panel from './Panel'

const Panels = ({ panels, isEditingPanel, handleCreatePanel }) => {
  const handleAddPanel = () => {
    handleCreatePanel()
  }

  return (
    <Box sx={{ mt: 2 }}>
      {
        panels.map(({ isEditing, id, coordinates }) => <Panel key={id} isEditing={isEditing} panelId={id} coordinates={coordinates} />)
      }
      <Button variant="contained" fullWidth endIcon={<AddIcon />} onClick={handleAddPanel} sx={{ mt: 2 }} disabled={isEditingPanel}>
        Add roof panel
      </Button>
    </Box>
  )
}

export default Panels