import GoogleMapReact from 'google-map-react'
import { MapApiKey, GoogleLibs } from '../constants'

const Map = ({ children, handleApiLoaded, handleOnClick }) => {
  const defaultProps = {
    center: {
      lat: 51.5072,
      lng: 0.1276
    },
    zoom: 6
  }
  
  return (
    <GoogleMapReact
      bootstrapURLKeys={{ key: MapApiKey, version: 'quarterly', libraries: GoogleLibs }}
      defaultCenter={defaultProps.center}
      defaultZoom={defaultProps.zoom}
      yesIWantToUseGoogleMapApiInternals
      onGoogleApiLoaded={handleApiLoaded}
      options={{ mapTypeId: 'satellite' }}
      onClick={handleOnClick}
    >
      {children}
    </GoogleMapReact>
  )
}

export default Map