import { useRef, useEffect } from 'react'
import TextField from '@mui/material/TextField'

const AutoComplete = ({ maps, handlePlaceChange }) => {
  const input = useRef(null)

  useEffect(() => {
    if (input.current && maps && handlePlaceChange) {
      const autoComplete = new maps.places.Autocomplete(
        input.current,
        {},
      )
      autoComplete.addListener('place_changed', () => {
        handlePlaceChange(autoComplete.getPlace())
      })
    }
  }, [input, maps, handlePlaceChange])
  
  return (
    <TextField inputRef={input} label="Enter a location" fullWidth />
  )
}

export default AutoComplete